.pages-base-root {
  width: 100%;

  .homePage-000 {
    flex: 1 0 auto;
    position: relative;
    width: 100%;
    -webkit-transition: all .5s ease-in;
    -o-transition: all .5s ease-in;
    transition: all .5s ease-in;
    .intro_section {
      .pageBody {
        .grid {
          .user-info {
            margin: auto;
            display: flex;
            flex-direction: column;
            align-items: center;
            p {
              font-size: 17px;
              text-align: center;
            }
            .home0buttons {
              margin: 0 5px;
            }
          }
          .user-dp {
            border-radius: 40px;
          }
        }
      }
    }
  }
  
}